export class Vector{
    private elements : number[];
    readonly length : number;

    constructor(a : number[]);
    constructor(n : number);
    constructor(v : Vector)
    constructor(a : unknown){
        if(typeof a == "number"){
            this.length = a;
            this.elements = new Array<number>(a);
            for(let i = 0;i<this.length;i++){
                this.elements[i] = 0;
            }
            return;
        }
        if(a instanceof Array){
            this.length = a.length;
            this.elements = a;
            return;
        }
        if(a instanceof Vector){
            this.length = a.length;
            this.elements = new Array<number>(this.length);
            for(let i = 0;i < a.length;i ++){
                this.elements[i] = a.at(i);
            }
            return;
        }
        console.error("„Çà„Åè„Çè„Åã„Çâ„ÇìÂûã„ÅÆÂ•¥„ÅåÂëº„Å≥Âá∫„Åï„Çå„Åü");
    }

    public at(i: number) : number {
        return this.elements[i];
    }
    
    public set(i: number, value : number) :void {
        if(i < 0 || i >= this.length){
            console.error("you cannot access the area")
            return;
        } 
        this.elements[i]  = value;
    }

    public map(func : (e : number) =>number) : Vector {
        let a :Vector = new Vector(this.elements.map(func));
        return a;
    }

    public add(b : Vector) : Vector 
    public add(b :number[]) : Vector 
    public add(b : unknown) :Vector{
        if(b instanceof Vector){

            if(b.length != this.length) {
                console.error("the number of elements are not same");
                return;
            }
            const a :number[] = this.elements.concat();
            for(let i = 0; i < this.length;i++){
                a[i] += b.at(i);
            }
            return new Vector(a);
        }
        if(b instanceof Array){
            if(b.length != this.length) {
                console.error("the number of elements are not same");
                return;
            }
            const a :number[] = this.elements.concat();
            for(let i = 0; i < this.length;i++){
                a[i] += b[i];
            }
            return new Vector(a);
        }
        console.error("invalid argument");
        
    }

    public mul(c : number) : Vector {
        let a = new Vector(this);
        return a.map((e :number) => e * c);
    }
    
    public div(c : number) {
        if(c == 0) console.error("do not divide with 0")
        let a = new Vector(this);
        return a.map((e :number) => e / c);
    }

    public mod(c : number) {
        if(c == 0) console.error("do not mod with 0")
        let a = new Vector(this);
        return a.map((e :number) :number => e % c);
    }

    //index„ÇíÂÖ•„ÇåËæº„ÇÄ„Åø„Åü„ÅÑ„Å™„Åì„Å®„Çí„Åô„Çã python(numpy)„ÅÆ[]ÊºîÁÆóÂ≠ê„Åø„Åü„ÅÑ„Å™ÊÑü„Åò
    public apply_move(move :Vector) : Vector {
        let ret_v : Vector = new Vector(this);
        if(ret_v.length != move.length ) {
            console.error("the number of elements are not same")
            return ;
        }
        for(let i = 0;i < this.length;i++){
            ret_v.set(i,this.at(move.at(i)));
        }
        return ret_v;
    }

    public each_is_in_limitation(end : number) : Boolean {
        let is_ok :Boolean = true;
        for(let i = 0; i < this.length ; i++){
            if(this.elements[i] >= end) is_ok = false;
        }
        return is_ok;
    }
}

